<template>
  <el-container class="container">
    <el-header class="header-box">
      <p class="blog-title"> MY BLOG</p>
      <div>
        <p class="username">{{username}}</p>
        <p @click="singout" class="singout">[退出]</p>
      </div>
    </el-header>
    <el-container class="container-box">
      <el-aside class="aside-box" width="150px">
        <el-menu
          background-color='#3b6997' 
          text-color='#ffffff' 
          active-text-color="#ffd04b"
          unique-opened
          class="el-menu-vertical-demo"
          >
          <el-submenu :index="item.id" v-for="(item,index) in asideMenuData" :key="index">
            <template slot="title"><i class="el-icon-message"></i>{{item.title}}</template>
            <el-menu-item
            v-for="(itemson,indexson) in item.submeun" :key='indexson'
             :index="itemson.id">{{itemson.title}}</el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
// import {mapState} from 'vuex'
  export default {
    data(){
      return{
        username:'',
        asideMenuData:[
          {
            title:'HTML',
            id:'1',
            submeun:[
              {
                title:'1111',
                id:'1-1'
              },
              {
                title:'222',
                id:'1-2'
              }
            ]
          },
          {
            title:'CSS',
            id:'2',
            submeun:[
              {
                title:'1111',
                id:'2-1'
              },
              {
                title:'222',
                id:'2-2'
              }
            ]
          },
          {
            title:'JS',
            id:'3',
            submeun:[
              {
                title:'1111',
                id:'3-1'
              },
              {
                title:'222',
                id:'3-2'
              }
            ]
          }
        ]
      }
    },
    computed:{
      // ...mapState([
      //   'username'
      // ])
    },
    methods:{
      singout:function(){
        localStorage.removeItem('token');
        this.$router.push({
          path:'/login'
        })
      }
    },
    mounted(){
      this.username = localStorage.getItem('username')
    }
  }
</script>

<style scoped>
 .header-box{
   background:#1a446d;
   color:#fff;
   text-align: center;
   line-height: 60px;
   position: relative;
 }
 .header-box p.blog-title{
   position: absolute;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   margin:auto;
   line-height: 60px;
   height: 60px;
 }
 .header-box div{
   position: absolute;
   right: 30px;
   height: 60px;
   line-height: 60px;
 }
 .header-box div p{
   float:left;
   font-size: 14px;
   color:skyblue;
   line-height: 60px;
   cursor: pointer;
   margin-left:10px;
 }
 .aside-box{
   height:100%;
   background:#3b6997;
 }
 .container-box,.container{
   height:100%;
 }
 .el-submenu .el-menu-item{
   min-width: 0;
 }
</style>


